import { Component, ViewChild, ViewEncapsulation, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { clone, cloneDeep, isEmpty, union } from 'lodash';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';

import { Employee, EmployeeList, EmployeeService } from 'modules/employee/employee.service';
import { Department, DepartmentService } from 'modules/organization/department/department.service';
import { Name } from 'modules/user/profile/profile.service';
import Utility from 'shared/helpers/utility';
import { NgForm } from '@angular/forms';
import { ExaTask } from '../../../core/providers/exatask.service';

@Component({
  templateUrl: './manage-employee.component.html',
  providers: [EmployeeService, DepartmentService],
  encapsulation: ViewEncapsulation.None
})
export class ManageEmployeeComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('employeeForm') public employeeForm: NgForm;

  employee: Employee = new Employee();
  original_employee: Employee = new Employee();
  employee_id: string = null;

  error: string = '';

  departments: Department[] = new Array();
  designations: string[] = new Array();
  supervisors: EmployeeList[] = new Array();
  super_admins: EmployeeList[] = new Array();
  allDepartmentSupervisors: Map<string, EmployeeList[]> = new Map<string, EmployeeList[]>();

  constructor(
    private route: ActivatedRoute,
    private employeeService: EmployeeService,
    private departmentService: DepartmentService,
    public exatask: ExaTask
  ) {

    this.exatask.setTitle('Manage Employee');
    this.departmentService.getDepartments()
      .then (depts => {
        this.departments = depts;
        },
      err => this.error = 'Error getting department info');

    if (this.route.snapshot.params.id) {
      this.employee_id = this.route.snapshot.params.id;
    }
  }

  ngOnInit() {

    if (this.employee_id) {
      this.__initializeEmployee(this.employee_id);
    } else {
      this.employee_id = null;
    }
  }

  private __initializeEmployee(id: string) {

    this.employeeService.getEmployeeDetails(id)
      .then(response => {

        if (response.department) {
          response.department = response.department.department_id;
        }
        if (response.supervisor) {
          response.supervisor = response.supervisor.supervisor_id;
        }

        this.employee = response;
        this.original_employee = cloneDeep(this.employee);
        this.exatask.setTitle('Manage Employee : ' + Name.getFullName(this.employee.name));

        if (this.employee.department) {
          this.selectDepartment();
        }
      });
  }

  private __sanitize(data) {

    if (!data.supervisor) {
      data.supervisor = null;
    }

    if (!data.is_chief_executive) {
      data.is_chief_executive = false;
    }

    // If middle name is empty (whether null or empty string)
    if (data.name && isEmpty(data.name.middle)) {
      data.name.middle = null;
    }

    return data;
  }

  private __sanitizeUpdate(data, original_data) {

    if (data.name) {

      if (!data.name.first) {
        data.name.first = original_data.name.first;
      }
      if (!data.name.last) {
        data.name.last = original_data.name.last;
      }
      if (data.name.middle === original_data.name.middle) {
        data.name.middle = original_data.name.middle;
      }
    }

    // If middle name is empty (whether null or empty string)
    if (data.name && isEmpty(data.name.middle)) {
      data.name.middle = null;
    }

    return data;
  }

  saveEmployee() {

    this.error = '';

    if (this.employee_id) {

      let employee_data = Utility.compare(this.original_employee, this.employee);
      if (employee_data !== undefined) {

        employee_data = this.__sanitizeUpdate(employee_data, this.original_employee);
        if (Object.keys(employee_data).length === 0) {
          return;
        }

        this.employeeService.putEmployee(this.employee_id, employee_data)
          .then(response => {

            this.successModal.show('/employee');

          }).catch(err => {

            this.error = err.error.message.text;
            this.errorModal.show();
          });
      }

    } else {

      let employee_data = cloneDeep(this.employee);
      employee_data = this.__sanitize(employee_data);
      this.employeeService.postEmployee(employee_data)
        .then(response => {

          this.successModal.show('/employee');

        }).catch(err => {

          this.error = err.error.message.text;
          this.errorModal.show();
        });
    }
  }

  resetEmployee() {

    if (this.employee_id) {
      this.employee = cloneDeep(this.original_employee);
    } else {
      this.employeeForm.reset();
    }
  }

  addAnother() {

    this.employee = new Employee();
    this.successModal.hide(true);
  }

  selectDepartment() {

    const department_id = this.employee.department;

    const projection = ['_id', 'name', 'email_id'];

    const query1 = new Map<string, any>();
    query1.set('department_id', department_id);
    query1.set('is_supervisor', true);

    const query2 = new Map<string, any>();
    query2.set('is_super_admin', true);

    const self = this;

    for (const department of this.departments) {

      if (department._id === department_id) {
        this.designations = department.designations;
        break;
      }
    }

    if (this.allDepartmentSupervisors.has(department_id)) {

      this.supervisors = this.allDepartmentSupervisors.get(department_id);

    } else {

      const dataPromises = [];
      let dataPromise;
      dataPromise = this.employeeService.getEmployees(query1, 0, 0, projection).then(response => {

        for (const user of response.users) {
          user.name = Name.getFullName(user.name);
        }
        self.supervisors = response.users;
      });
      dataPromises.push(dataPromise);

      dataPromise = this.employeeService.getEmployees(query2, 0, 0, projection).then(response => {

        for (const user of response.users) {
          user.name = Name.getFullName(user.name);
        }
        self.super_admins = response.users;
      });
      dataPromises.push(dataPromise);

      Promise.all(dataPromises).then(response => {

        const user_ids = [];
        self.supervisors.forEach(function (user) {
          user_ids.push(user._id.toString());
        });

        self.super_admins.forEach(function (user) {
          if (user_ids.indexOf(user._id.toString()) === -1) {
            self.supervisors.push(user);
          }
        });

        self.allDepartmentSupervisors[department_id] = self.supervisors;
      });
    }
  }
}
