import { Component, OnInit, ViewChild } from '@angular/core';
import { EmployeeService, EmployeeDetails } from 'modules/employee/employee.service';
import { ActivatedRoute } from '@angular/router';
import { Name } from 'modules/user/profile/profile.service';
import { ExaTask } from '../../../core/providers/exatask.service';
import { Access } from '../../../core/providers/access.service';
import { UserService } from '../../user/user.service';
import { SuccessModalComponent } from '../../../shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from '../../../shared/components/error-modal/error-modal.component';
import { STATUS } from '../../../config/constants/user';

@Component({
  selector: 'app-employee-detail',
  templateUrl: './employee-detail.component.html',
  providers: [EmployeeService, UserService]
})
export class EmployeeDetailComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  employee: EmployeeDetails = new EmployeeDetails();
  employee_id: string;

  loading: boolean = false;
  inProgress: any;
  successMessage: string;
  errorMessage: string;

  updateAllowed: boolean;
  accessControlAllowed: boolean;
  markSupervisorAllowed: boolean;
  unmarkSupervisorAllowed: boolean;
  markChiefExecutiveAllowed: boolean;
  unmarkChiefExecutiveAllowed: boolean;
  markSuperAdminAllowed: boolean;
  unmarkSuperAdminAllowed: boolean;
  unlockUserAllowed: boolean;
  resetPasswordAllowed: boolean;

  constructor(
    private route: ActivatedRoute,
    private employeeService: EmployeeService,
    private userService: UserService,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.exatask.setTitle('Employee Details');
    this.employee_id = this.route.snapshot.params.id;

    this.inProgress = {
      unlockUser: false,
      markAsSupervisor: false,
      unmarkAsSupervisor: false,
      markAsChiefExecutive: false,
      unmarkAsChiefExecutive: false,
      markAsSuperAdmin: false,
      unmarkAsSuperAdmin: false,
      resetPassword: false
    };
  }

  async ngOnInit() {

    this.__getEmployeeDetails(this.employee_id);

    this.updateAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'employee',
      action: 'update'
    });
    this.accessControlAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'access-control',
      action: 'update'
    });
    this.markSupervisorAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'supervisor',
      action: 'mark'
    });
    this.unmarkSupervisorAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'supervisor',
      action: 'unmark'
    });
    this.markChiefExecutiveAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'chief-executive',
      action: 'mark'
    });
    this.unmarkChiefExecutiveAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'chief-executive',
      action: 'unmark'
    });
    this.markSuperAdminAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'super-admin',
      action: 'mark'
    });
    this.unmarkSuperAdminAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'super-admin',
      action: 'unmark'
    });
    this.unlockUserAllowed = await this.access.hasAccess({
      module: 'user',
      entity: 'user',
      action: 'unlock'
    });
    this.resetPasswordAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'employee',
      action: 'reset'
    });
  }

  private __getEmployeeDetails(id) {

    this.loading = true;
    this.employeeService.getEmployeeDetails(id)
      .then(response => {

        this.loading = false;
        this.employee = response;
        this.employee.full_name = Name.getFullName(this.employee.name);
        this.exatask.setTitle('Employee Details : ' + this.employee.full_name);
      });
  }

  unlockUser() {

    this.inProgress.unlockUser = true;
    const self = this;

    this.userService.unlockUser(this.employee_id)
      .then(response => {

        self.employee.user_status = STATUS.ACTIVE;
        self.inProgress.unlockUser = false;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.unlockUser = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  markAsSupervisor() {

    this.inProgress.markAsSupervisor = true;
    const self = this;

    this.employeeService.markAsSupervisor(this.employee_id)
      .then(response => {

        self.inProgress.markAsSupervisor = false;
        self.employee.is_supervisor = true;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.markAsSupervisor = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  unmarkAsSupervisor() {

    this.inProgress.unmarkAsSupervisor = true;
    const self = this;

    this.employeeService.unmarkAsSupervisor(this.employee_id)
      .then(response => {

        self.inProgress.unmarkAsSupervisor = false;
        self.employee.is_supervisor = false;
        self.employee.is_chief_executive = false;
        self.employee.is_super_admin = false;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.unmarkAsSupervisor = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  markAsChiefExecutive() {

    this.inProgress.markAsChiefExecutive = true;
    const self = this;

    this.employeeService.markAsChiefExecutive(this.employee_id)
      .then(response => {

        self.inProgress.markAsChiefExecutive = false;
        self.employee.is_chief_executive = true;
        self.employee.is_supervisor = true;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.markAsChiefExecutive = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  unmarkAsChiefExecutive() {

    this.inProgress.unmarkAsChiefExecutive = true;
    const self = this;

    this.employeeService.unmarkAsChiefExecutive(this.employee_id)
      .then(response => {

        self.inProgress.unmarkAsChiefExecutive = false;
        self.employee.is_chief_executive = false;
        self.employee.is_super_admin = false;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.unmarkAsChiefExecutive = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  markAsSuperAdmin() {

    this.inProgress.markAsSuperAdmin = true;
    const self = this;

    this.employeeService.markAsSuperAdmin(this.employee_id)
      .then(response => {

        self.inProgress.markAsSuperAdmin = false;
        self.employee.is_super_admin = true;
        self.employee.is_chief_executive = true;
        self.employee.is_supervisor = true;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.markAsSuperAdmin = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  unmarkAsSuperAdmin() {

    this.inProgress.unmarkAsSuperAdmin = true;
    const self = this;

    this.employeeService.unmarkAsSuperAdmin(this.employee_id)
      .then(response => {

        self.inProgress.unmarkAsSuperAdmin = false;
        self.employee.is_super_admin = false;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.unmarkAsSuperAdmin = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }

  resetPassword() {

    this.inProgress.resetPassword = true;
    const self = this;

    this.employeeService.resetPassword(this.employee_id)
      .then(response => {

        self.inProgress.resetPassword = false;
        self.successMessage = response.message.text;
        self.successModal.show();

      }).catch(err => {

      self.inProgress.resetPassword = false;
      self.errorMessage = err.error.message.text;
      self.errorModal.show();
    });
  }
}
