import { Component, OnInit, ViewChild } from '@angular/core';
import { EmployeeList, EmployeeService } from 'modules/employee/employee.service';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { Access } from 'core/providers/access.service';
import { STATUS } from '../../../config/constants/user';
import { UserService } from '../../user/user.service';
import { ExaTask } from '../../../core/providers/exatask.service';

@Component({
  selector: 'app-users',
  templateUrl: './list-employee.component.html',
  providers: [EmployeeService, UserService]
})
export class ListEmployeeComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  employees: EmployeeList[];
  count: number;
  limit: number;
  page: number;
  total_pages: number;

  loading: boolean = false;
  inProgress: Array<boolean>;
  successMessage: string;
  errorMessage: string;

  createAllowed: boolean;
  exportAllowed: boolean;

  constructor(
    private employeeService: EmployeeService,
    private userService: UserService,
    private access: Access,
    public exatask: ExaTask
  ) {

    this.employees = [];
    this.limit = 25;
    this.createAllowed = false;
    this.inProgress = new Array<boolean>();
    this.exatask.setTitle('Employee List');
  }

  async ngOnInit() {

    this.getEmployees(1);

    this.createAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'employee',
      action: 'create'
    });

    this.exportAllowed = await this.access.hasAccess({
      module: 'employee',
      entity: 'employee',
      action: 'export'
    });
  }

  getEmployees(page) {

    const self = this;

    this.page = page;
    this.loading = true;
    this.employeeService.getEmployees(null, this.page, this.limit).then(response => {

      self.loading = false;
      self.count = response.count;
      self.employees = response.users;

      self.total_pages = self.count / self.limit;
      self.total_pages += (self.count % self.limit) === 0 ? 0 : 1;
    });
  }

  exportEmployees() {

    const self = this;

    this.employeeService.exportEmployees().then(response => {

      this.successMessage = response.message.text;
      this.successModal.show('/employee');

    }).catch(err => {

      this.errorMessage = err.error.message.text;
      this.errorModal.show();
    });
  }

  getStatusBadge(status) {

    switch (status) {

      case STATUS.ACTIVE: return 'badge-success';
      case STATUS.LOCKED: return 'badge-warning';
      default: return 'badge-default';
    }
  }
}
