import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ManageEmployeeComponent } from './manage-employee/manage-employee.component';
import { UploadEmployeeComponent } from './upload-employee/upload-employee.component';
import { ListEmployeeComponent } from './list-employee/list-employee.component';
import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { ObjectidGuard } from 'core/services/objectid.service';
import { EmployeeDetailComponent } from './employee-detail/employee-detail.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Employee'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'feedback',
        loadChildren: './feedback/feedback.module#FeedbackModule'
      },
      {
        path: 'access-control',
        loadChildren: './employee-access-control/employee-access-control.module#EmployeeAccessControlModule'
      },
      {
        path: 'leave-policy',
        loadChildren: './employee-leave-policy/employee-leave-policy.module#EmployeeLeavePolicyModule'
      },
      {
        path: 'wfh-policy',
        loadChildren: './employee-wfh-policy/employee-wfh-policy.module#EmployeeWfhPolicyModule'
      },
      {
        path: 'list',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'List',
          access: {
            module: 'employee',
            entity: 'employee',
            action: 'find'
          }
        },
        component: ListEmployeeComponent
      },
      {
        path: 'add',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Add',
          access: {
            module: 'employee',
            entity: 'employee',
            action: 'create'
          }
        },
        component: ManageEmployeeComponent
      },
      {
        path: 'upload',
        canActivate: [AuthenticationGuard, AuthenticationGuard],
        data: {
          title: 'Upload',
          access: {
            module: 'employee',
            entity: 'employee',
            action: 'create'
          }
        },
        component: UploadEmployeeComponent
      },
      {
        path: ':id',
        canActivate: [AuthenticationGuard, AuthenticationGuard, ObjectidGuard],
        data: {
          title: 'Details',
          access: {
            module: 'employee',
            entity: 'employee',
            action: 'find'
          }
        },
        component: EmployeeDetailComponent
      },
      {
        path: 'edit/:id',
        canActivate: [AuthenticationGuard, AuthorizationGuard, ObjectidGuard],
        data: {
          title: 'Update',
          access: {
            module: 'employee',
            entity: 'employee',
            action: 'update'
          }
        },
        component: ManageEmployeeComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeeRoutingModule { }
