import { Component, ViewEncapsulation, ViewChild } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { EmployeeService, Employee, EmployeeList } from '../employee.service';
import { DepartmentService, Department } from 'modules/organization/department/department.service';
import { Name } from 'modules/user/profile/profile.service';
import { clone } from 'lodash';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ExaTask } from '../../../core/providers/exatask.service';

@Component({
  selector: 'app-upload-employee',
  templateUrl: './upload-employee.component.html',
  providers: [EmployeeService, DepartmentService],
  encapsulation: ViewEncapsulation.None
})
export class UploadEmployeeComponent {
  @ViewChild('successModal') successModal: SuccessModalComponent;

  downloadLink: string = environment.apiServer + '/documents/Bulk Employee Upload.xlsx';
  acceptedFormats: string = '.csv,.xlsx,.xls';

  uploadedFile: File;
  employees: Employee[];
  departments: Department[] = new Array();
  designations: string[][] = new Array();
  supervisors: EmployeeList[][] = new Array();
  super_admins: EmployeeList[];

  fileName: string = 'Choose File';
  error: string = '';

  uploading: boolean = false;
  uploaded: boolean = false;
  savingAll: boolean = false;
  saving: boolean[];
  saved: boolean[];
  saveStatus: string[];

  constructor(
    private employeeService: EmployeeService,
    private departmentService: DepartmentService,
    public exatask: ExaTask
  ) {
    this.exatask.setTitle('Upload Employee');
  }

  selectFile($event) {

    this.uploadedFile = $event.target.files[0];
    this.fileName = this.uploadedFile.name;
  }

  uploadFile() {

    if (!this.uploadedFile) {
      return;
    }

    const self = this;
    this.uploading = true;

    this.employeeService.uploadBulkEmployee(this.uploadedFile)
      .then(response => {

        if (response.data.length === 0) {
          this.error = 'No employee data was uploaded';
          return;
        }

        self.employees = response.data;
        self.uploaded = true;
        self.saving = new Array(self.employees.length);
        self.saved = new Array(self.employees.length);
        self.saveStatus = new Array(self.employees.length);
        this.uploading = false;

        const dataPromises = [];
        let dataPromise;

        dataPromise = self.departmentService.getDepartments().then(depts => {
          self.departments = depts;
        });
        dataPromises.push(dataPromise);

        const query = new Map<string, any>();
        query.set('is_super_admin', true);
        const projection = ['_id', 'name', 'email_id'];

        dataPromise = self.employeeService.getEmployees(query, 0, 0, projection).then(employee_response => {

          for (const user of employee_response.users) {
            user.name = Name.getFullName(user.name);
          }
          self.super_admins = employee_response.users;
        });
        dataPromises.push(dataPromise);

        Promise.all(dataPromises).then(() => {

          self.departments.forEach(function (department) {
            self.selectDepartment(department._id);
          });
        });

      }).catch(err => {

        self.error = err.error.message.text;
        self.uploading = false;
      });
  }

  selectDepartment(department_id) {

    if (!this.designations[department_id]) {

      for (const department of this.departments) {

        if (department._id === department_id) {
          this.designations[department_id] = department.designations;
          break;
        }
      }
    }

    if (!this.supervisors[department_id]) {

      const query = new Map<string, any>();
      query.set('department_id', department_id);
      query.set('is_supervisor', true);

      const projection = ['_id', 'name', 'email_id'];
      const self = this;

      this.employeeService.getEmployees(query, 0, 0, projection).then(response => {

        for (const user of response.users) {
          user.name = Name.getFullName(user.name);
        }
        self.supervisors[department_id] = response.users;

        const user_ids = [];
        response.users.forEach(function (user) {
          user_ids.push(user._id.toString());
        });

        self.super_admins.forEach(function (user) {
          if (user_ids.indexOf(user._id.toString()) === -1) {
            self.supervisors[department_id].push(user);
          }
        });
      });
    }
  }

  saveEmployees() {

    const self = this;
    const savePromises = [];

    this.employees.forEach(function (employee, index) {

      self.saving[index] = true;
      const employee_data = clone(employee);
      employee_data.is_chief_executive = false;

      const savePromise = self.employeeService.postEmployee(employee_data).then(response => {

        self.saving[index] = false;
        self.saved[index] = true;
        self.saveStatus[index] = '';

      }).catch(err => {

        self.saving[index] = false;
        self.saved[index] = true;
        self.saveStatus[index] = err.error.message.text;
      });
      savePromises.push(savePromise);
    });

    Promise.all(savePromises).then(response => {

      let all_success = true;
      self.saveStatus.forEach(function (status) {
        if (status.length !== 0) {
          all_success = false;
        }
      });

      if (all_success) {
        this.successModal.show('/employee');
      }
    });
  }
}
